import {LitElement, html, css, customElement, property} from 'lit-element';
import {isMobile} from '../../../utils/response-query';
import '../../molecules/uc-header-list-popup/uc-header-list-popup';

@customElement('uc-header-basket')
class UcHeaderBasket extends LitElement {
    @property({type: Number}) count = 0;
    @property({type: String}) dataList = '';

    static styles = css`
        :host {
            position: relative;
            display: flex;
            align-items: center; }
        @media (min-width: 992px) {
        :host {
                padding: 10px;
                border-top: 1px solid transparent;
                border-left: 1px solid transparent;
                border-right: 1px solid transparent;
                z-index: 3;
                background-color: #fff; } }
        @media (max-width: 991px) {
        :host {
                overflow: hidden; } }
        
        @media (min-width: 992px) {
        :host(:hover) {
                border-top: 1px solid #eee;
                border-left: 1px solid #eee;
                border-right: 1px solid #eee; }
        :host(:hover) uc-header-list-popup {
                display: flex; }
        :host(:hover)::after {
                position: absolute;
                content: '';
                height: 1px;
                width: 100%;
                left: 0;
                bottom: 0;
                z-index: 4;
                background-color: #fff; } }
        
        ::slotted(a) {
            height: 100%;
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0; }
        
        .basket-list-icon {
            position: relative;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat; }
        @media (min-width: 992px) {
        .basket-list-icon {
                width: 36px;
                height: 30px;
                background-image: url(data:image/svg+xml;base64,); } }
        @media (max-width: 991px) {
        .basket-list-icon {
                background-image: url(data:image/svg+xml;base64,); } }
        @media (max-width: 991px) and (min-width: 768px) {
        .basket-list-icon {
                width: 27px;
                height: 22px; } }
        @media (max-width: 767px) {
        .basket-list-icon {
                width: 1.92857em;
                height: 1.57143em; } }
        
        .basket-list-icon:not([data-count='0'])::after {
            position: absolute; }
        @media (min-width: 992px) {
        .basket-list-icon:not([data-count='0'])::after {
                font-size: 11px;
                border-radius: 2px;
                content: attr(data-count);
                background: #000;
                top: -5px;
                right: -6px;
                color: #ffffff;
                line-height: 12px;
                padding: 0 3px 2px; } }
        @media (max-width: 991px) and (min-width: 768px) {
        .basket-list-icon:not([data-count='0'])::after {
                width: 6px;
                height: 6px;
                font-size: 11px; } }
        @media (max-width: 767px) {
        .basket-list-icon:not([data-count='0'])::after {
                font-size: 0.78571em;
                width: 0.54545em;
                height: 0.54545em; } }
        @media (max-width: 991px) {
        .basket-list-icon:not([data-count='0'])::after {
                content: '';
                background: #ff0000;
                top: 0;
                right: 0;
                border-radius: 50%;
                padding: 0; } }
        
        .basket-list-text {
            font-size: 11px;
            font-weight: bold;
            line-height: 1.27;
            width: 53px;
            text-transform: uppercase;
            color: #333333;
            margin-left: 10px;
            margin-right: 10px; }
        @media (max-width: 991px) {
        .basket-list-text {
                display: none; } }
        
        uc-header-list-popup {
            z-index: -1;
            position: absolute;
            top: calc(100% - 1px);
            right: -1px;
            display: none; }
    `;

    render() {
        return html`
            <slot></slot>
            <div class="basket-list-icon" data-count="${this.count}"></div>
            ${isMobile() ? html`` : html`
                <div class="basket-list-text">КОРЗИНА</div>
                <uc-header-list-popup datatype="buy-list" datalist="${this.dataList}"></uc-header-list-popup>
            `}
        `;
    }
}